.productdisplay
{
    display: flex;
    margin: 0px 150px;

}

.productdisplay-left
{
    display: flex;
    gap: 15px;
}

.productdisplay-img-list
{
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.productdisplay-img-list img
{
    height: 120px;

}

.productdisplay-main-img
{
    width: 450px;
    height: 470px;
}

.productdisplay-right
{
    margin: 0px 40px;
    display: flex;
    flex-direction: column;
}

.productdisplay-right h1
{
    color: #3d3d3d;
    font-size: 30px;
    font-weight: 700;
}

.productdisplay-right-star
{
    display: flex;
    align-items: center;
    margin-top: 13px;
    gap: 0px;
    color: #1c1c1c;
    font-size: 12px;
}

.productdisplay-right-prices
{
    display: flex;
    margin: 25px 0px;
    gap: 15px;
    font-size: 24px;
    font-weight: 700;
}

.productdisplay-right-price-old
{
    color: #818181;
    text-decoration: line-through;

}

.productdisplay-right-price-new
{
    color: #ff4141;
}

.productdisplay-right- h1
{
    margin-top: 40px;
    color: #656565;
    font-size: 16px;
    font-weight: 600;
    
}

.productdisplay-right-sizes
{
    display: flex;
    margin: 30px 0px;
    gap: 20px;
}

.productdisplay-right-sizes div
{
    padding: 12px 20px;
    background: #fbfbfb;
    border: 1px solid #ebebeb;
    border-radius: 3px;
    cursor: pointer;
}
.productdisplay-right button
{
    padding: 14px 30px ;
    width: 180px;
    font-size: 16px;
    font-weight: 600;
    color: white;
    background-color: #ff4141;
    margin-bottom: 40px;
    border: none;
    outline: none;
    cursor: pointer;

}

.productdisplay-right-category
{
    margin-top: 0px;
}

.productdisplay-right-category span
{
    font-weight: 600;
}